import React, { Component } from 'react'

export class Body extends Component {
    render() {
        return (
            <div className="container">
                <div className="calc__body">
                    <div className="calc__math">
                        <button className="calc__math__sign" name="+" onClick={e => this.props.onClick(e.target.name)}>+</button>
                        <button className="calc__math__sign" name="-" onClick={e => this.props.onClick(e.target.name)}>-</button>
                        <button className="calc__math__sign" name="*" onClick={e => this.props.onClick(e.target.name)}>*</button>
                        <button className="calc__math__sign" name="/" onClick={e => this.props.onClick(e.target.name)}>/</button>
                        <button className="calc__math__sign" name="CE" onClick={e => this.props.onClick(e.target.name)}>CE</button>
                        <button className="calc__math__sign clear" name="C" onClick={e => this.props.onClick(e.target.name)}>C</button>
                    </div>
                    <div className="calc__num__main">
                        <button className="calc__num" id="num00" name="00" onClick={e => this.props.onClick(e.target.name)}>00</button>

                        <button className="calc__num" id="num00" name="0" onClick={e => this.props.onClick(e.target.name)}>0</button>

                        <button className="calc__num" id="num1" name="1" onClick={e => this.props.onClick(e.target.name)}>1</button>

                        <button className="calc__num" id="num2"name="2" onClick={e => this.props.onClick(e.target.name)}>2</button>
                        
                        <button className="calc__num" id="num1" name="3" onClick={e => this.props.onClick(e.target.name)}>3</button>
                        <button className="calc__num" id="num4" name="4" onClick={e => this.props.onClick(e.target.name)}>4</button>
                        <button className="calc__num" id="num5" name="5" onClick={e => this.props.onClick(e.target.name)}>5</button>
                        <button className="calc__num" id="num6" name="6" onClick={e => this.props.onClick(e.target.name)}>6</button>
                        <button className="calc__num" id="nu70" name="7" onClick={e => this.props.onClick(e.target.name)}>7</button>
                        <button className="calc__num" id="num8" name="8" onClick={e => this.props.onClick(e.target.name)}>8</button>
                        <button className="calc__num" id="num9" name="9" onClick={e => this.props.onClick(e.target.name)}>9</button>
                        <button className="calc__num equal" id="equal" name="=" onClick={e => this.props.onClick(e.target.name)}>=</button>

                    </div>
                </div>
            </div>
        )
    }
}

export default Body
